::-webkit-scrollbar {
    width: 10px;
    background-color: rgb(255, 252, 252);
  }
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
    height: 10px;
  }
  ::-webkit-scrollbar-thumb {
    background: rgb(48, 36, 36);
    border-radius: 10px;
  }
  